<template>
    <div>
      <h3 class="dropList-title" @click="show">
          {{title}}
          <span class="dropList-outer" :class={showBorder:!showTittle,display:!items.length}>
            <span class="dropList-toBottom" v-if="showTittle">▼</span>
            <span class="dropList-toTop" v-if="!showTittle">▲</span>
          </span>
      </h3>
        <ul class="dropList-items" ref="items">
          <li v-for="one in items" class="dropList-item"><a href="#">{{one}}</a></li>
        </ul>
    </div>
</template>

<script>
  import 'owl.carousel/dist/assets/owl.carousel.css';
  import $ from 'jquery';
  import 'owl.carousel/dist/owl.carousel.min';
  export default {
        props:{
            title: String,
            items: Array
        },
        data(){
            return{
              showTittle:false
            }
        },
        methods:{
          show(){
              new Promise((resolve)=>{
                this.showTittle=!this.showTittle;
                resolve()
              }).then(()=>{
                $(this.$refs.items).slideToggle();
              })
          },
        },
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .dropList-title{
    font-size: 13px;
    background-color: #43494C;
    line-height: 44px;
    border-bottom: 1px solid #3C3C3C;
    padding-left: 5%;
    transition: background-color 1s,border-color 1s,color 1s;
    cursor: pointer;
    margin: 0;
    .showBorder{
      border-left:1px solid #fff;
      background-color: #212121;;
    }
    .dropList-outer{
      width: 44px;
      height: 44px;
      line-height: 44px;
      float: right;
      border: 1px solid #3C3C3C;
      box-sizing: border-box;
      text-align: center;
      .dropList-toBottom{
        display: inline-block;
      }
      .dropList-toTop{
        display: inline-block;
      }
    }
    &:hover{
      background-color: #3C3C3C;
    }
  }
  .dropList-items{
    display: none;
    .dropList-item{
      @extend .dropList-title;
      padding-left: 10%;
    }
  }
  .display{
    display: none;
  }
</style>



