<template>
  <div>
    <div class="nav-item-wrapper" :class={changeBg:display} @click="showItems">
      <div class="nav-item-link">
        {{title}}
          <span v-if="items.length">
          <i class="fa fa-angle-down fa-lg"></i>
        </span>
      </div>
    </div>
    <ul class="nav-item-drop" v-if="display" ref="box">
      <li v-for="(one,index) in items" class="nav-item-drop-item"  @click="showItems">
        <a href="#">{{one}}</a>
      </li>
    </ul>
  </div>
</template>

<script>
  import $ from 'jquery'
  import Vue from 'vue'
  export default {
    props:{
      title: String,
      items: Array
    },
    data(){
      return{
          display:false,
      }
    },
    methods:{
      showItems(){
          new Promise((resolve)=> {
            if (this.display){
              this.display=false;
            }else{
              this.$root.eventHub.$emit('changeBg')
              this.display = true;
              resolve();
            }
          }).then(()=> {
            this.fixPosition(this.$refs.box)
          });
      },
      fixPosition(element){
          let pos=element.getBoundingClientRect();
          if (pos.right>innerWidth){
              let fixValue=pos.right-innerWidth;
              element.style.marginLeft=-fixValue+'px';
          }
      }
    },
    mounted(){
      this.$root.eventHub.$on('changeBg', ()=> {
          this.display=false;
      });
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .nav-item-wrapper{
    width: 100%;
    &:hover{
      background-color: #4c4c4c;;
    }
    .nav-item-link{
      display: table-cell;
      vertical-align: middle;
      color: #fff;
      border-left: 1px solid rgba(0,0,0,.5);
      height: 45px;
      width: 100%;
      padding: 0 15px;
      text-align: center;
      font-size: 13px;
      box-sizing: border-box;
      cursor: pointer;
      text-decoration: none;
      .fa{
        position: absolute;
        right: 7px;
        top: 15px;
      }
    }
  }
  .nav-item-drop{
    position: absolute;
    display: inline-block;
    left:0;
    .nav-item-drop-item{
      display: block;
      padding: 13px 20px;
      font-weight: 400;
      line-height: 1.42857143;
      color: #333;
      white-space: nowrap;
      border: 1px solid #aaa;
      background-color: white;
      &:hover{
        background-color: #FF2896;;
      }
      a{
        color:#000;
      }
    }
  }
  .changeBg{
    background-color: #4c4c4c;
  }
</style>



