<template>
    <div class="sidebar-wrapper">
      <div class="sidebar-left">
        <ul class="sidebar-items">
          <li v-for="(one,index) in text">
            <drop-list :title="one.title" :items="one.items"></drop-list>
          </li>
          <drop-list :title="title" :items="items"></drop-list>
        </ul>
      </div>
      <div class="sidebar-right" @click="hideSidebar"></div>
    </div>
</template>

<script>
  import dropList from './droplist'
    export default {
        data(){
            return{
                title:'Financial Report',
                items:[
                    'Message from our Chairman and CEO','Leadership changes'
                ],
                text:[
                    {
                        title:'Year at a Glance',
                        items:[]
                    },
                    {
                        title:'Chairman and CEO Message',
                        items:['Message from our Chairman and CEO','Leadership changes']
                    },
                    {
                        title:'About Telstra',
                        items:['Our Business','Directors and Management Team','Governance at Telstra']
                    },
                    {
                        title:'Strategy and Performance',
                        items:['Overview','Improve Customer Advocacy','Drive Value from the Core','Build New Growth Businesses',
                        'Our Material Business Risks','Outlook']
                    },
                    {
                        title:'Results and Operations',
                        items:[]
                    },
                    {
                        title:'Sustainability',
                        items:['Our Approach','Customer Experience','Connecting Communities','Our People',
                        'Environmental Stewardship','Responsible Business']
                    },
                    {
                        title:'Directors’ Report',
                        items:['Directors’ Report','Remuneration Report']
                    },
                    {
                        title:'Financial Report',
                        items:['Financial Statements','Notes to the Financial Statements','Directors’ Declaration']
                    },
                    ]
            }
        },
        props:{
            isShow:{
                type:Boolean,
                default:false
            }
        },
        components:{
          dropList:dropList
        },
        methods:{
          hideSidebar(){
              this.$emit('hide')
          }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .sidebar-wrapper{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 10;
    .sidebar-left{
      position: absolute;
      width: 75%;
      left: 0;
      top: 0;
      bottom: 0;
      background-color: #212121;
      color: #fff;
      overflow: scroll;
    }
    .sidebar-right{
      position: absolute;
      width: 25%;
      right: 0;
      top: 0;
      bottom: 0;
    }
  }

</style>



