<template>
  <div class="carousel-wrapper">
    <div class="owl-carousel owl-theme">
      <div class="item" v-for="(one,index) in srcArray"><img :src='srcArray[index]' style="width: 100%;height: 100%"></div>
    </div>
  </div>
</template>

<script>
  import 'owl.carousel/dist/assets/owl.carousel.css';
  import $ from 'jquery';
  import 'owl.carousel/dist/owl.carousel.min';
  export default {
    data(){
      return {
        srcArray: ['https://telstra2015ar.interactiveinvestorreports.com/wp-content/themes/Telstra-Theme/imgs/tiles/KeyHighlights_050815_Slide1.jpg',
          'https://telstra2015ar.interactiveinvestorreports.com/wp-content/themes/Telstra-Theme/imgs/tiles/KeyHighlights_050815_Slide2.jpg',
          'https://telstra2015ar.interactiveinvestorreports.com/wp-content/themes/Telstra-Theme/imgs/tiles/KeyHighlights_050815_Slide3.jpg']
      }
    },
    mounted(){
      $(document).ready(() => {
        $('.owl-carousel').owlCarousel({
          margin: 0,
          loop: true,
          items: 1,
          autoplay: true,
          autoplayHoverPause: false,
          navClass:["nav-left",'nav-right'],
          nav:true,
          navText:['←','→'],
          dotsClass:'nav-dots',
          dotClass:'nav-dot',
          autoWidth:false,
          autoplayTimeout:2000,
          smartSpeed:700
        })
      });
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss">
  .carousel-wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    .owl-carousel{
      width: 100%;
      height: 100%;
      .owl-stage-outer{
        width: 100%;
        height: 100%;
        .owl-stage{
          width: 100%;
          height: 100%;
          .owl-item{
            width: 100%;
            height: 100%;
            .item{
              width: 100%;
              height: 100%;
            }
          }
      }
      }
    }
  }
  .nav-right {
    position: absolute;
    right: 10px;
    top: 45%;
    color: white;
    z-index: 15;
    cursor: pointer;
    font-size: 40px;
    font-weight: bold;
  }
  .nav-left {
    position: absolute;
    left: 10px;
    top: 45%;
    color: white;
    z-index: 15;
    cursor: pointer;
    font-size: 40px;
    font-weight: bold;
  }
  .nav-dots{
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    text-align: center;
    height: 20px;
    width: 100%;
    .nav-dot{
      height: 10px;
      width: 10px;
      border-radius: 50%;
      background-color: transparent;
      display: inline-block;
      border: 1px solid white;
      margin-left: 10px;
      cursor: pointer;
      &.active{
        background-color: white;
      }
    }
  }
  ::selection{
    background-color: transparent;
  }
</style>



