<template>
  <div>
    <h3 class="dropList-title" @click="show">
      <slot name="title">这是标题</slot>
      <span class="dropList-triangle" >
            <span class="dropList-toBottom" v-if="showTittle">▼</span>
            <span class="dropList-toTop" v-if="!showTittle">▲</span>
      </span>
    </h3>
    <div ref="items">
      <slot name="content">这是内容</slot>
    </div>
  </div>
</template>

<script>
  import 'owl.carousel/dist/assets/owl.carousel.css';
  import $ from 'jquery';
  import 'owl.carousel/dist/owl.carousel.min';
  export default {
    data(){
      return{
        showTittle:false
      }
    },
    mounted(){
      $(this.$refs.items).css({
        display:'none'
      });
    },
    methods:{
      show(){
        new Promise((resolve)=>{
          this.showTittle=!this.showTittle;
          resolve()
        }).then(()=>{
          $(this.$refs.items).slideToggle();
        })
      },
    },
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .dropList-title{
    cursor: pointer;
    position: relative;
    .dropList-triangle{
      position: absolute;
      right: 0;
      top: 0;
      width: 35px;
      height: 35px;
      line-height: 35px;
      color: white;
      background-color: #640064;
      border: 1px solid #3C3C3C;
      box-sizing: border-box;
      text-align: center;
      .dropList-toBottom{
        display: inline-block;
      }
      .dropList-toTop{
        display: inline-block;
      }
    }
    &:hover{
       background-color: #ddd;
     }
  }
  .display{
    display: none;
  }
</style>



