<template>
  <div>
    <div class="brand1-container">
      <a href="#" class="brand1-wrapper clearfix">
        <span class="brand1-content"></span>
        <span class="brand1-title">ANNUAL REPORT 2015</span>
      </a>
      <span class="brand1-search-wrapper">
        <form>
          <input type="text" placeholder="Search..." class="brand1-text"><button
           type="submit" class="brand1-button"><i class="fa fa-search fa-lg"></i></button>
        </form>
      </span>
      <span class="brand1-icon-wrapper">
        <span class="brand1-toggle-font"><small>A</small>A</span>
        <span class="brand1-email"><i class="fa fa-envelope-o fa-lg"></i></span>
        <span class="brand1-print"><i class="fa fa-print fa-lg"></i></span>
      </span>
      <span class="brand1-bars" @click="showSidebar" v-show="!isShow">
        <i class="fa fa-bars fa-lg"></i>
      </span>
    </div>
    <transition name="fade">
      <sidebar-component v-if="isShow" @hide="hideSidebar"></sidebar-component>
    </transition>
  </div>
</template>

<script>
  import sidebar from '../components/sidebar'
export default {
  data () {
    return {
        isShow:false
    }
  },
  components:{
      sidebarComponent:sidebar
  },
  methods:{
    showSidebar(){
          this.isShow=!this.isShow;
    },
    hideSidebar(){
        this.isShow=false
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .fade-enter{
    transform: translateX(-75%);
  }
  .fade-enter-active{
    transition: all 1s ease;
  }
  .fade-leave-to{
    transform: translateX(-100%);
  }
  .fade-leave-active{
    transition: all 1s ease;
  }
  $height:40px;
  .brand1-container{
    height: $height;
    width: 970px;
    margin: 0 auto;
    .brand1-wrapper{
      display: inline-block;
      .brand1-content {
        float: left;
        width: 28px;
        height: $height;
        background: url('https://telstra2015ar.interactiveinvestorreports.com/wp-content/themes/Telstra-Theme/imgs/telstra-logo.png') no-repeat;
        background-position: -150px 3px;
      }
      .brand1-title{
        float: left;
        font-family: Gravur,GravurCondensed-Regular,Helvetica Neue,Arial Narrow,Helvetica,Arial,sans-serif;
        text-transform: uppercase;
        font-size: 17px;
        color: #FF2896;
        margin-left: 5px;
        margin-top: 10px;
      }
    }
    .brand1-icon-wrapper{
      float: right;
      height: $height;
      line-height: $height;
      .brand1-toggle-font{
        padding-left: 7px;
        padding-right: 7px;
        font-weight: 400;
        cursor: pointer;
        font-size: 20px;
        color: #999;
      }
      .brand1-email{
        padding-left: 7px;
        padding-right: 7px;
        cursor: pointer;
        color: #999;
      }
      .brand1-print{
        padding-left: 7px;
        padding-right: 7px;
        cursor: pointer;
        color: #999;
      }
    }
    .brand1-search-wrapper{
      float: right;
      height: $height;
      position: relative;
      .brand1-text{
        height: 100%;
        background-color:#dadada;
        top: 8px;
        left:15px;
        position: relative;
      }
      .brand1-button{
        position: relative;
        background-color: transparent;
        border: none;
        left: -15px;
        top: 6px;
        outline: none;
        cursor: pointer;
      }
    }
    .brand1-bars{
      color: #a0148c;
      float: right;
      position: relative;
      top: 10px;
      right: 10px;
      display: none;
    }
    @media only screen and (max-width: 992px) {
      &{
        width: 100%;
      }
      .brand1-icon-wrapper,.brand1-search-wrapper{
        display: none;
      }
      .brand1-bars{
        display: block;
      }
    }
  }
</style>



