<template>
  <div class="img-wrapper"  @mouseenter="showOverlay=true,showFont=false" @mouseleave="showOverlay=false,showFont=true">
    <div v-bind:style='{backgroundImage:"url("+src+")"}' class="img-bgWrapper"></div>
    <div class="img-text-wrapper" :style="{backgroundColor:'rgba('+color+',.8)'}">
      <transition v-on:before-enter="beforeEnter2"
                  v-on:enter="enter2"
                  v-on:before-leave="beforeLeave2"
                  v-on:leave="leave2">
        <span v-if="showFont">{{title}}</span>
      </transition>
    </div>
    <transition    v-on:before-enter="beforeEnter"
                   v-on:enter="enter"
                   v-on:leave="leave"
    >
      <div class="img-overlay" v-if="showOverlay" :style="{backgroundColor:'rgba('+color+',.5)'}">
        <div class="centerLayout">{{title}}</div>
      </div>
    </transition>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        showOverlay:false,
        showFont:true,
      }
    },
    props:{
      src:String,
      title:String,
      color:String,
    },
    methods:{
      beforeEnter(el) {
        $(el).css({
          fontSize:"10px",
          height:'20px',
          position:'absolute',
          bottom:'0px',
          left:'0',
          right:'0',
          top:'215px'
        })
      },
      enter(el) {
        $(el).animate({
          fontSize:'23px',
          top:'0',
          width:'100%',
          height:'100%'
        },1000)
      },
      leave(el,done){
        $(el).animate({
          display:'block',
          fontSize:"10px",
          height:'20px',
          top:'215px'
        },1000,function () {
          $(el).css({
            display:'none',
          })
        })
      },
      beforeLeave2(el) {
        $(el).css({
          fontSize:"20px",
        })
      },
      leave2(el,done) {
        $(el).animate({
          fontSize:'10px',
        },1000,()=>{
          $(el).css({
            fontSize:"0",
          })
        })
      },
      beforeEnter2(el){
        $(el).css({
          fontSize:"10px",
        })
      },
      enter2(el,done){
        $(el).animate({
          fontSize:"20px",
        },1000)
      },
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .img-wrapper{
    position: relative;
    cursor:pointer;
    width: 100%;
    height: 100%;
    .img-bgWrapper{
      width: 100%;
      height: 100%;
      background-position: center;
      background-repeat: repeat;
    }
    .img-text-wrapper{
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 66px;
      line-height: 66px;
      font-size: 20px;
      color: white;
      transition: all .5s ease-in-out;
      text-align: left;
      padding: 5px 20px;
    }
    .img-overlay{
      position: absolute;
      left:0;
      right: 0;
      bottom: 0;
      top: 0;
      color: white;
      font-size: 23px;
      font-family: Gravur,GravurCondensed-Regular,Helvetica Neue,Arial Narrow,Helvetica,Arial,sans-serif;
      text-transform: uppercase;
      line-height: normal;
      transform: translate3d(0,0,0);
    }
  }
</style>



