<template>
    <div>
      <brand-component></brand-component>
      <div ref="nav" :class={fix:fixed}>
        <nav-component></nav-component>
      </div>
      <grid-component></grid-component>
      <main-component></main-component>
      <footer-component></footer-component>
    </div>
</template>

<script>
  import brand from '../components/brand-section'
  import nav from '../components/nav'
  import grid from '../components/grid'
  import main from '../components/main'
  import footer from '../components/footer'
    export default {
      data(){
          return{
              fixed:false
          }
      },
      components:{
          brandComponent:brand,
          navComponent:nav,
          gridComponent:grid,
          mainComponent:main,
          footerComponent:footer
      },
      methods:{
      },
      mounted(){
        $(document).mouseup((event)=>{
          var nodeList=[];
          var node=$('nav')[0];
          depthTraversal(node);
          function depthTraversal(node){
            if (node!=null)
            {
              nodeList.push(node);
              for (var i=0;i<node.children.length;i++)
              {
                depthTraversal(node.children[i])
              }
            }
          }
          let result=nodeList.indexOf(event.target);
          if (result===-1){
            this.$root.eventHub.$emit('changeBg')
          }
        });
        $(document).scroll(()=>{
            if ($('body')[0].scrollTop>=40){
                this.fixed =true
            }
            if ($('body')[0].scrollTop<40){
              this.fixed=false
            }
        })
      }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .fix{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 100;
  }
</style>



